import JustValidate from 'just-validate';
const validate = () => {
  const telSelector = document.querySelector('input[type="tel"]')
  const inputmask = new Inputmask("+1 (999) 999 99 99", {
    showMaskOnHover: false
  });
  inputmask.mask(telSelector)
  const validation = new JustValidate('.contacts__form', '.footer__form');
  validation
    .addField('#name', [{
        rule: 'required',
        value: 3,
        errorMessage: 'Enter your name',
      },
      {
        rule: 'maxLength',
        value: 30,
      },
    ])
    .addField('#tel', [{
        rule: 'required',
        errorMessage: 'Telephone is required',
      },
      {
        rule: 'function',
        validator: function () {
          const phone = telSelector.inputmask.unmaskedvalue()
          return phone.length === 10;
        },
        errorMessage: 'Telephone is invalid!',
      },
    ])
    .addField('#email', [{
        rule: 'required',
        errorMessage: 'Email is required',
      },
      {
        rule: 'email',
        errorMessage: 'Email is invalid!',
      },
    ]).onSuccess((event) => {
      let formData = new FormData(event.target)
      let xhr = new XMLHttpRequest()
      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {
          if (xhr.status === 200) {
            console.log('Sent')
          }
        }
      }
      xhr.open('POST', 'mail.php', true)
      xhr.send(formData)
      event.target.reset()
    })
}
export default validate