const tabs = () => {
const tabs = document.querySelectorAll('.features__btn-item')
const tabsContent = document.querySelectorAll('.features__panel')
const tabsParent = document.querySelector('.features__items')
function hideTabsContent() {
    tabsContent.forEach(item => {
        item.classList.add('hidden')
        item.classList.remove('active')
    })
    tabs.forEach(item => {
        item.classList.remove('active__btn')
    })
}
function showTabsContent(i = 0) {
    tabsContent[i].classList.add('active')
    tabsContent[i].classList.remove('hidden')
    tabs[i].classList.add('active__btn')
}
hideTabsContent()
showTabsContent()
tabsParent.addEventListener('click', (e) => {
    const target = e.target
    if (target && target.classList.contains('features__btn-item')) {
        tabs.forEach((item, i) => {
            if (target == item) {
                hideTabsContent()
                showTabsContent(i)
            }
        })
    }
})
}
export default tabs