const typetext = () => {
    const t = [
        'Look Stylish Be Stylish.'
    ]
        let line = 0
        let count = 0
        let outText = ''
        const outTextContent = document.querySelector('.headertop__title')
    function typeLine() {
        let interval = setTimeout(() => {
            outText += t[line][count]
            outTextContent.innerHTML = outText + '|'
            count ++
            if (count >= t[line].length) {
                count = 0
                line ++
                if (line == t.length) {
                    clearTimeout(interval)
                    outTextContent.innerHTML = outText
                    return  true
                }
            }
            typeLine()
        }, getRandomInt(getRandomInt(350 * 2.5)));
    }
    typeLine()
    function getRandomInt(max) {
        return Math.floor(Math.random() * Math.floor(max))
    }
}
export default typetext